'use client';

import { createSlice } from '@reduxjs/toolkit';

// üß± Initial state for the audio player
const initialState = {
  playlist: [],               // üéµ Full list of track objects
  currentTrackIndex: 0,       // üî¢ Which song is playing
  currentTrack: null,         // üé∂ Full track object (optional but useful)
  isPlaying: false,           // ‚ñ∂Ô∏è or ‚è∏
  isReady: false,             // ‚úÖ Amplitude initialized
};

// üéöÔ∏è Slice
const audioSlice = createSlice({
  name: 'audio',
  initialState,
  reducers: {
    // üéµ Load playlist into state
    setPlaylist(state, action) {
      state.playlist = action.payload;
    },

    // ‚ñ∂Ô∏è Play specific track by index
    playTrack(state, action) {
      const index = action.payload;
      state.currentTrackIndex = index;
      state.currentTrack = state.playlist[index];
      state.isPlaying = true;
    },

    // ‚è∏ Pause track
    pauseTrack(state) {
      state.isPlaying = false;
    },

    // ‚è≠ Next track
    nextTrack(state) {
      const nextIndex = (state.currentTrackIndex + 1) % state.playlist.length;
      state.currentTrackIndex = nextIndex;
      state.currentTrack = state.playlist[nextIndex];
    },

    // ‚èÆ Previous track
    prevTrack(state) {
      const prevIndex = (state.currentTrackIndex - 1 + state.playlist.length) % state.playlist.length;
      state.currentTrackIndex = prevIndex;
      state.currentTrack = state.playlist[prevIndex];
    },

    // ‚ûï Add to playlist
    addToPlaylist(state, action) {
      const newTrack = action.payload;
      const exists = state.playlist.some(t => t.url === newTrack.url);
      if (!exists) {
        state.playlist.push(newTrack);
      }
    },

    // üîÑ Set playback state manually
    setIsPlaying(state, action) {
      state.isPlaying = action.payload;
    },

    // ‚úÖ Ready flag after Amplitude.init()
    setIsReady(state, action) {
      state.isReady = action.payload;
    },

    // üéØ Set current full track (metadata)
    setCurrentTrack(state, action) {
      state.currentTrack = action.payload;
    },

    // üî¢ Set current track index manually
    setCurrentTrackIndex(state, action) {
      state.currentTrackIndex = action.payload;
    },
  },
});

export const {
  setPlaylist,
  playTrack,
  pauseTrack,
  nextTrack,
  prevTrack,
  addToPlaylist,
  setIsPlaying,
  setIsReady,
  setCurrentTrack,           // ‚úÖ Newly added
  setCurrentTrackIndex,      // ‚úÖ Added to fix existing usage
} = audioSlice.actions;

export default audioSlice.reducer;
